.home-section{

 display:flex;
 flex-wrap: wrap;
    
}

.text-col {
    width: 50vw;
    float: left;
     /* Should be removed. Only for demonstration */
    padding-top: 0.5%;
    padding-right: 7%;
   
    
}

.picture{
    
    width: 45vw;
    border-radius: 200px;
    float: right;
    
    /* Should be removed. Only for demonstration */
    padding-top: 13%;
    padding-left: 8%;

    
     
    
  }


  .picture img {
border-radius: 1em;
 
    
  }
/* üste dokunm açalışııyor */ 




 /* media Quaries */

 @media( max-width: 1100px ) {

    .text-col{   
        width: 80%;
        display: block;
      text-align: center;
      margin-left: 10%;
      margin-right: 10%;        
        padding-top: 1%;

        padding-right: 0%;
         
        }

        
    .picture{     
        width: 80%;
        display: block;
        margin-left: 10%;
        margin-right: 10%;
        
        padding-top: 15%;
        
        padding-left: 0%;
        

    }
    .picture img {
        border-radius: 1em;
        padding-top: 2%;
            
          }

    .mainbtn{

 
        width: 80%;
        display: block;
        text-align: center;
        margin-left: 10%;
        margin-right: 10%;
        
      
    }

}




